<template>
    <div class="first">
    <!-- <FirstTop v-bind:topData="topData"></FirstTop>
    <FirstBottom v-bind:shopRate="shopRate"></FirstBottom> -->
    <div class="leftItem">
        <div id="leftEchart"></div>
    </div>
    <div class="firstBottom">
        <div class="firstShop">
            <div id="firstChart"></div>
        </div>
        <div class="secondShop">
            <div id="secondChart"></div>
        </div>
        <div class="thirdShop">
            <div id="thirdChart"></div>
        </div>
    </div> 
    </div>
</template>

<script>
export default {
  name: 'First',
  data () {
    return {
		url:"",
        intervalId: 0,
        shopRate:{
          "firstShopRate":0,
          "secondShopRate":100,
          "thirdShopRate":0
        },
        topData:{
          firstShop: {
            "plan": 1,
            "actual": 0
          },
          secondShop:{
            "plan": 0, 
            "actual": 0
          },
          thirdShop: {
            "plan": 0, 
            "actual": 0
          }
        } 
    }
  },
  created () {
      this.getNewData();
      this.intervalId = setInterval(() => {
            this.getNewData()
      },600000)                           //10分钟进行一次请求
  },
  beforeDestroy () {
        clearInterval(this.intervalId)
  },
  methods:{
    //axios请求后台数据
    getNewData(){
		this.url ='http://' + window.location.host;
		console.log(this.url);
      	this.$axios.get(`${this.url}/dafeng_mes/bug1/workshopComplete`)
		.then(res => {
            console.log(res);
            let myData = res.data.varList;
            if(myData){
              myData.reverse();
              console.log("反转后的数据");
              console.log(myData);
              console.log("反转后的数据");
              this.topData.firstShop.plan = parseInt(myData[0].PLAN_AMOUNT);
              this.topData.firstShop.actual = parseInt(myData[0].PRODUCT_AMOUNT);
              //console.log(this.topData.firstShop.plan);
              if(this.topData.firstShop.plan == 0){
                   this.shopRate.firstShopRate = 0;
              }else{
                  this.shopRate.firstShopRate = parseInt(this.topData.firstShop.actual/this.topData.firstShop.plan).toFixed(2)*100;
              }
              this.topData.secondShop.plan = parseInt(myData[1].PLAN_AMOUNT);
              this.topData.secondShop.actual = parseInt(myData[1].PRODUCT_AMOUNT);
              if(this.topData.secondShop.plan == 0){
                   this.shopRate.secondShopRate = 0;
              }else{
                  this.shopRate.secondShopRate = parseInt(this.topData.secondShop.actual/this.topData.secondShop.plan).toFixed(2)*100;
              }
              this.topData.thirdShop.plan = parseInt(myData[2].PLAN_AMOUNT);
              this.topData.thirdShop.actual = parseInt(myData[2].PRODUCT_AMOUNT);
              if(this.topData.thirdShop.plan == 0){
                   this.shopRate.thirdShopRate = 0;
              }else{
                  this.shopRate.thirdShopRate = parseInt(this.topData.thirdShop.actual/this.topData.thirdShop.plan).toFixed(2)*100;
              }
              }
              else
              {
              this.topData.firstShop.plan = 0;
              this.topData.firstShop.actual = 0;
              this.shopRate.firstShopRate = 0;
              this.topData.secondShop.plan = 0;
              this.topData.secondShop.actual = 0;
              this.shopRate.secondShopRate = 0;
              this.topData.thirdShop.plan = 0;
              this.topData.thirdShop.actual = 0;
              this.shopRate.thirdShopRate = 0;
            } 
            this.drawLeftEcahrt();
            this.drawFirstShop();
            this.drawSecondShop();
            this.drawThirdShop();       
        });
    },
    drawLeftEcahrt(){
      let leftEchart = this.$echarts.init(document.getElementById('leftEchart'));
      var option_workshop = {
      title : {
      text: '当日计划完成情况',
      x:30,
      y:10,
      textStyle:{
        color:'#fff',
        fontSize:25
      }
  },
        grid:{
            x:50                   
        },
        legend: {
          x:320,
              y:10,
              textStyle: {
                    color: '#fff'
              }		                	
        },
        tooltip: {},
        dataset: {
            // 这里指定了维度名的顺序，从而可以利用默认的维度到坐标轴的映射。
            // 如果不指定 dimensions，也可以通过指定 series.encode 完成映射，参见后文。
            dimensions: ['product', '计划', '实际'],
            source: [
                {product: '一车间', '计划': this.topData.firstShop.plan, '实际':this.topData.firstShop.actual},
                {product: '二车间', '计划': this.topData.secondShop.plan, '实际': this.topData.secondShop.actual},
                {product: '三车间', '计划': this.topData.thirdShop.plan, '实际': this.topData.thirdShop.actual}
            ]
        },
        xAxis: {
          type: 'category',
          axisLine: {
                show: false
            },
            axisLabel: {
                show: true,
                  textStyle: {
                    color: '#fff',  //更改坐标轴文字颜色	    	                   
                  }
              },
            axisTick:{
              show: false		   //显示/隐藏刻度
            }	            	
        },
        yAxis: {
          axisLine: {
                show: false
            },
            axisLabel: {
                show: true,
                  textStyle: {
                    color: '#fff',  //更改坐标轴文字颜色	    	                   
                  }
              },
            axisTick:{
              show: false		   //显示/隐藏刻度
            },
        },
        series: [
          {
                  type: 'bar',
                  barWidth:25,
        itemStyle: {
            normal: {
              color:'#cf1322',
                label: {
                    show: true,
                    position: 'top',
                    textStyle: {
                        color: '#fff'
                    }
                }
            }
        }
            },
            {
                  type: 'bar',
                  barWidth:25,
        itemStyle: {
            normal: {
              color:'#fadb14',
                label: {
                    show: true,
                    position: 'top',
                    textStyle: {
                        color: '#fff'
                    }
                }
            }
        }
            }
        ]	           
        };
        leftEchart.setOption(option_workshop);
  },
  drawFirstShop(){
        // 基于准备好的dom，初始化echarts实例
        let firstChart = this.$echarts.init(document.getElementById('firstChart'));
        var firstOption = {
            title : {
		        text: '一车间',
		        x:'center',
		        y:10,
		        textStyle:{
		        	color:'#fff',
		        	fontSize:25
		        }
		    },
            tooltip : {
                formatter: "{a} <br/>{b} : {c}%"
            },
            series : [
                {
                    name:'工作进度',
                    type:'gauge',
                    startAngle: 180,
                    itemStyle:{
                        color: 'rgba(255,255,255,0.6)',
                        
	                },
                    endAngle: 0,
                    center : ['50%', '90%'],    // 默认全局居中
                    radius : 95,
                    axisLine: {            // 坐标轴线
                        lineStyle: {       // 属性lineStyle控制线条样式
                            color: [[0.09, 'lime'],[0.82, '#1e90ff'],[1, '#ff4500']],
                            width: 3,
                            shadowColor : '#fff', //默认透明
                            shadowBlur: 10
                        }
                    },
                    axisTick: {            // 坐标轴小标记
                        splitNumber: 5,   // 每份split细分多少段
                        length :12,        // 属性length控制线长
                    },
                    axisLabel: {           // 坐标轴文本标签，详见axis.axisLabel
                        formatter: function(v){
                            switch (v+''){
                                case '10': return '低';
                                case '50': return '';
                                case '90': return '高';
                                default: return '';
                            }
                        },
                        textStyle: {       // 其余属性默认使用全局文本样式，详见TEXTSTYLE
                            color: '#fff',
                            fontSize: 15,
                            fontWeight: 'bolder'
                        }
                    },
                    pointer: {
                        width:8,
                        length: '80%',
                        color: 'rgba(255, 255, 255, 0.8)'
                    },
                    title : {
                        show : true,
                        offsetCenter: [0, '-60%'],       // x, y，单位px
                        textStyle: {       // 其余属性默认使用全局文本样式，详见TEXTSTYLE
                            color: '#fff',
                            fontSize: 15
                        }
                    },
                    detail : {
                        show : true,
                        backgroundColor: 'rgba(0,0,0,0)',
                        borderWidth: 0,
                        borderColor: 'red',
                        width: 80,
                        height: 30,
                        offsetCenter: [0, -30],       // x, y，单位px
                        formatter:'{value}%',
                        textStyle: {       // 其余属性默认使用全局文本样式，详见TEXTSTYLE
                            fontSize : 25,
                            color:'#fff'
                        }
                    },
                    data:[{value: this.shopRate.firstShopRate, name: '按时完工率'}]
                }
            ]};       
        // 绘制图表
        firstChart.setOption(firstOption);
        },
        drawSecondShop(){
             // 基于准备好的dom，初始化echarts实例
        let secondChart = this.$echarts.init(document.getElementById('secondChart'));
        var secondOption = {
            title : {
		        text: '二车间',
		        x:'center',
		        y:10,
		        textStyle:{
		        	color:'#fff',
		        	fontSize:25
		        }
		    },
            tooltip : {
                formatter: "{a} <br/>{b} : {c}%"
            },
            series : [
                {
                    name:'业务指标',
                    type:'gauge',
                    startAngle: 180,
                    itemStyle:{
                        color: 'rgba(255,255,255,0.6)',
                        
	                },
                    endAngle: 0,
                    center : ['50%', '90%'],    // 默认全局居中
                    radius : 95,
                    axisLine: {            // 坐标轴线
                        lineStyle: {       // 属性lineStyle控制线条样式
                            color: [[0.09, 'lime'],[0.82, '#1e90ff'],[1, '#ff4500']],
                            width: 3,
                            shadowColor : '#fff', //默认透明
                            shadowBlur: 10
                        }
                    },
                    axisTick: {            // 坐标轴小标记
                        splitNumber: 5,   // 每份split细分多少段
                        length :12,        // 属性length控制线长
                    },
                    axisLabel: {           // 坐标轴文本标签，详见axis.axisLabel
                        formatter: function(v){
                            switch (v+''){
                                case '10': return '低';
                                case '50': return '';
                                case '90': return '高';
                                default: return '';
                            }
                        },
                        textStyle: {       // 其余属性默认使用全局文本样式，详见TEXTSTYLE
                            color: '#fff',
                            fontSize: 15,
                            fontWeight: 'bolder'
                        }
                    },
                    pointer: {
                        width:8,
                        length: '80%',
                        color: 'rgba(255, 255, 255, 0.8)'
                    },
                    title : {
                        show : true,
                        offsetCenter: [0, '-60%'],       // x, y，单位px
                        textStyle: {       // 其余属性默认使用全局文本样式，详见TEXTSTYLE
                            color: '#fff',
                            fontSize: 15
                        }
                    },
                    detail : {
                        show : true,
                        backgroundColor: 'rgba(0,0,0,0)',
                        borderWidth: 0,
                        borderColor: 'red',
                        width: 80,
                        height: 30,
                        offsetCenter: [0, -30],       // x, y，单位px
                        formatter:'{value}%',
                        textStyle: {       // 其余属性默认使用全局文本样式，详见TEXTSTYLE
                            fontSize : 25,
                            color:'#fff'
                        }
                    },
                    data:[{value: this.shopRate.secondShopRate, name: '按时完工率'}]
                }
            ]};       
        // 绘制图表
        secondChart.setOption(secondOption);
        },
        drawThirdShop(){
             // 基于准备好的dom，初始化echarts实例
        let thirdChart = this.$echarts.init(document.getElementById('thirdChart'));
        var thirdOption = {
            title : {
		        text: '三车间',
		        x:'center',
		        y:10,
		        textStyle:{
		        	color:'#fff',
		        	fontSize:25
		        }
		    },
            tooltip : {
                formatter: "{a} <br/>{b} : {c}%"
            },
            series : [
                {
                    name:'业务指标',
                    type:'gauge',
                    startAngle: 180,
                    itemStyle:{
                        color: 'rgba(255,255,255,0.6)',
                        
	                },
                    endAngle: 0,
                    center : ['50%', '90%'],    // 默认全局居中
                    radius : 95,
                     axisLine: {            // 坐标轴线
                        lineStyle: {       // 属性lineStyle控制线条样式
                            color: [[0.09, 'lime'],[0.82, '#1e90ff'],[1, '#ff4500']],
                            width: 3,
                            shadowColor : '#fff', //默认透明
                            shadowBlur: 10
                        }
                    },
                    axisTick: {            // 坐标轴小标记
                        splitNumber: 5,   // 每份split细分多少段
                        length :12,        // 属性length控制线长
                    },
                    axisLabel: {           // 坐标轴文本标签，详见axis.axisLabel
                        formatter: function(v){
                            switch (v+''){
                                case '10': return '低';
                                case '50': return '';
                                case '90': return '高';
                                default: return '';
                            }
                        },
                        textStyle: {       // 其余属性默认使用全局文本样式，详见TEXTSTYLE
                            color: '#fff',
                            fontSize: 15,
                            fontWeight: 'bolder'
                        }
                    },
                    pointer: {
                        width:8,
                        length: '80%',
                        color: 'rgba(255, 255, 255, 0.8)'
                    },
                    title : {
                        show : true,
                        offsetCenter: [0, '-60%'],       // x, y，单位px
                        textStyle: {       // 其余属性默认使用全局文本样式，详见TEXTSTYLE
                            color: '#fff',
                            fontSize: 15
                        }
                    },
                    detail : {
                        show : true,
                        backgroundColor: 'rgba(0,0,0,0)',
                        borderWidth: 0,
                        borderColor: 'red',
                        width: 80,
                        height: 30,
                        offsetCenter: [0, -30],       // x, y，单位px
                        formatter:'{value}%',
                        textStyle: {       // 其余属性默认使用全局文本样式，详见TEXTSTYLE
                            fontSize : 25,
                            color:'#fff'
                        }
                    },
                    data:[{value: this.shopRate.thirdShopRate, name: '按时完工率'}]
                }
            ]};       
        // 绘制图表
        thirdChart.setOption(thirdOption);
        }   
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
*{
    margin: 0;
    padding: 0;
}
.first{
    width: 100%;
    text-align: center;
    margin-top: 1%;
}
.leftItem{
    width: 100%;
    height:240px;
    border: 1px rgba(238, 238, 238, 0.4)  solid;
    border-radius: 5px;
    /* background: rgba(0, 0, 102, 0.2) */
}
#leftEchart{
    width: 100%;
    height: 250px;
}
.firstBottom{
    width: 100%;
    text-align: center;
    position: relative;
}
.firstShop{
    float: left;
    width: 32%;
    height:260px;
    border: 1px rgba(238, 238, 238, 0.4)  solid;
    border-radius: 5px;
    /* background: rgba(0, 0, 102, 0.2) ; */
    margin-top: 5px; 
}
.secondShop{
    float: left;
    width: 32%;
    height:260px;
    border: 1px rgba(238, 238, 238, 0.4)  solid;
    border-radius: 5px;
    /* background: rgba(0, 0, 102, 0.2); */
    margin-left: 2px;
     margin-top: 5px; 
}
.thirdShop{
    float: left;
    width: 33%;
    height:260px;
    border: 1px rgba(238, 238, 238, 0.4)  solid;
    border-radius: 5px;
    /* background: transparent; */
    margin-left: 2px;
    margin-top: 5px; 
}
#firstChart{
    display: inline-block;
    width: 200px;
    height: 200px;
}
#secondChart{
    display: inline-block;
    width: 200px;
    height: 200px;
}
#thirdChart{
    display: inline-block;
    width: 200px;
    height: 200px;
}
</style>



